

// main - heading
.heading-primary{
    font-family: $ff-heading;
    color: #000;
    font-size: 4.8rem;
    @include respond(tab-land){
        font-size: 4.0rem;
    }
    @include respond(tab-port){
        font-size: 3.0rem;
    }
    @include respond(phone){
        font-size: 2.6rem;
    }
}

.heading-secondary{
    font-family: $ff-heading;
    color: #333;
    font-size: 3.4rem;
    @include respond(tab-land){
        font-size: 3.2rem;
    }
    @include respond(tab-port){
        font-size: 3.2rem;
    }
    @include respond(phone){
        font-size: 3.0rem;
    }
}

.heading-tertiary {
    font-family: $ff-heading;
    color: #333;
    font-size: 2.4rem;

    @include respond(tab-land) {
        font-size: 2.4rem;
    }

    @include respond(tab-port) {
        font-size: 2.2rem;
    }

    @include respond(phone) {
        font-size: 2.0rem;
    }
}

